@keyframes scale-up-center {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.35);
  }

  100% {
    transform: scale(1);
  }
}

.app-link {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.app-link:hover, .app-link:focus {
  animation: scale-up-center .7s cubic-bezier(.39, .575, .565, 1);
}

.app-link:before, .app-link:after {
  content: "";
  position: absolute;
}

.app-link:before {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(90deg, #00c5eb -2%, #b544fe 90%);
  border-radius: 50%;
}

.app-link:after {
  width: calc(100% - 2px);
  height: calc(100% - 2px);
  z-index: 10;
  opacity: 0;
  background: #06071b;
  border-radius: 50%;
  transition: all .3s cubic-bezier(.39, .575, .565, 1);
  transform: scale(.01);
}

.app-link:hover:after, .app-link:focus:after {
  opacity: 1;
  transform: scale(1);
}

.app-icon {
  z-index: 11;
  fill: #fff;
}

