.container {
	// breakpoints
	// - mobile: верстка гумова, стає адаптивною на 480px;
	// - tablet: 768px;
	// - desktop: 1280px
	max-width: 480px;
	padding-left: 20px;
	padding-right: 20px;
	margin-left: auto;
	margin-right: auto;
	@media screen and (min-width: 768px) {
	  max-width: 768px;
	  padding-left: 32px;
	  padding-right: 32px;
	}
	@media screen and (min-width: 1280px) {
	  max-width: 1280px;
	  padding-left: 16px;
	  padding-right: 16px;
	}
  }